// ];

export default function Home() {
  const [posts, setPosts] = useState([]);
  const [aboutme, setAboutme] = useState([]);
  const [options, setOptions] = useState([{ value: "", label: "all" }]);
  const [seletedOption, setSelectedOption] = useState([]);
  const [imageGallery, setImageGallery] = useState([]);
  const [recentWrok, setRecentWork] = useState([]);
  const [skip, setSkip] = useState(0);
  const [isMoreContent, setIsMoreContent] = useState(true);
  const [category, setCategory] = useState([]);
  const { search } = useLocation();

  useEffect(() => {
    const initialization = async () => {
      // alert('hi')
      if (isMoreContent) {
        try {
          // alert("hi");
          let query = `?offset=${skip}&limit=15&search=${seletedOption}`;
          const blogPostResult = await actions.getBlogPost(query);

          if (skip == 0) setPosts(blogPostResult?.data?.results);
          else setPosts([...posts, ...blogPostResult?.data?.results]);
          if (blogPostResult?.data?.results.length < 10)
            setIsMoreContent(false);
        } catch (error) {
          console.log(error, "this is the eror ");
        }
      }
    };
    initialization();
  }, [search, skip, seletedOption]);
  useEffect(() => {
    const initialization = async () => {
      const categoryResult = await actions.getCategory();
      const aboutMe = await actions.getAboutMe();
      const recentWork = await actions.getRecentWork();
      const imageGallery = await actions.getImageGallery();
      const options = [{ value: "", label: "all" }];
      categoryResult.data?.map((item) => {
        options.push({ value: item.name, label: item.name });
      });
      setAboutme(aboutMe.data);
      setRecentWork(recentWork.data);
      setImageGallery(imageGallery.data);
      setOptions(options);
      setCategory(categoryResult.data);
    };
    initialization();
  }, [search]);
  return (
    <>
      <Header imageGallery={imageGallery} aboutme={aboutme} />
      <div className="recent-block">
        <div className="recent-block-img">
          <img src={recentWrok[0]?.photo} className="recent-img1" />
          {recentWrok[1]?.photo && (
            <img className="recent-img2" src={recentWrok[1]?.photo} />
          )}
          {recentWrok[2]?.photo && (
            <img className="recent-img3" src={recentWrok[2]?.photo} />
          )}
        </div>
        <div className="recent-block-text">
          <img
            src={imagePath.about}
            style={{
              width: 100,
              height: 100,
              zIndex: 1,
            }}
          />
          <p style={{ fontFamily: "fantasy" }}>
            <span style={{ fontWeight: "bold" }}>5000+ hours</span> of popular
            Indian and International shows across{" "}
            <span style={{ fontWeight: "bold" }}>200+ category</span>
          </p>
          <Select
            onChange={(value) => {
              setSelectedOption(value.value);
              setIsMoreContent(true);
            }}
            options={options}
          />
        </div>
      </div>
      <div className="home">
        <Posts posts={posts} />
      </div>
      <div className="about-block">
        <div className="about-img">
          <img
            src={aboutme[0]?.photo}
            style={{
              width: 300,
              height: 300,
              zIndex: 1,
            }}
          />
        </div>
        <div
          className="about-me"
          style={{ paddingLeft: 24, textAlign: "justify", letterSpacing: 2 }}
        >
          <div className="about-header">
            <img
              src={imagePath.aboutDialog}
              style={{
                alignContent: "center",
                // position: "absolute",
                // bottom: 0,
                width: 200,
                height: 100,
                zIndex: 1,
              }}
            />
          </div>
          <p style={{ lineHeight: 1.5, fontFamily: "cursive" }}>
            {aboutme[0]?.desc}
          </p>
        </div>
      </div>
    </>
  );
}
