import React, { useContext } from "react";
import "./header.css";
import AwesomeSlider from "react-awesome-slider";
import "react-awesome-slider/dist/styles.css";
// import 'react-awesome-slider/dist/custom-animations/cube-animation.css';
//importing typewriter-effect
import Typewriter from "typewriter-effect";
import FloatinDiv from "../FloatingDiv/FloatingDiv";
import { motion } from "framer-motion/dist/framer-motion";
import { Link } from "react-scroll";
import imagePath from "../../constant/imagePath";

const Header = (props) => {
  const { imageGallery, aboutme } = props;
  // console.log(aboutme[0], "this is aboutme ");
  const transition = { duration: 5, type: "spring" };
  return (
    <div className="intro">
      <div className="i-left">
        <motion.img
          initial={{ top: "25%", left: "30%" }}
          whileInView={{ left: "40%" }}
          transition={transition}
          src={imagePath.message3}
          style={{ width: "90px" }}
          alt=""
        />
        <div className="i-name">
          <Typewriter
            options={{ autoStart: true, loop: true }}
            onInit={(typewriter) => {
              typewriter
                .typeString(aboutme[0]?.headerText1 || "Hi there!, I am Nilesh")
                .pauseFor(5000)
                .deleteAll()
                .typeString(
                  aboutme[0]?.headerText2 || "Welcomes to my blogpost"
                )
                .start();
            }}
          />
        </div>

        <img
          src={aboutme[0]?.headerImage}
          style={{
            position: "absolute",
            bottom: 0,
            width: "95%",
            height: "50%",
            zIndex: 1,
          }}
        />
      </div>
      <div className="i-right">
        <AwesomeSlider bullets={false} animation="cubeAnimation">
          {imageGallery?.map((item) => (
            <div key={item.id} data-src={item.photo}></div>
          ))}
        </AwesomeSlider>
      </div>
    </div>
  );
};

export default Header;
