import axios from "axios";
import {
  ABOUTME,
  BLOG_POST,
  CATEGORY,
  IMAGEGALLERY,
  RECENTWORK,
} from "../apis/urls";

export const getBlogPost = (query = "") => axios.get(BLOG_POST + query);
export const getCategory = (query = "") => axios.get(CATEGORY + query);
export const getAboutMe = (query = "") => axios.get(ABOUTME + query);
export const getRecentWork = (query = "") => axios.get(RECENTWORK + query);
export const getImageGallery = (query = "") => axios.get(IMAGEGALLERY + query);
